.centered-header {
  text-align: center;
}

.centered-container2 {
  position: relative;
}

.resizable-image {
  width: 300px;
  height: 200px;
  border: 2px solid #ccc;
  background-color: #eee;
  position: absolute;
  resize: both;
  overflow: hidden;
}

.resizable-image::after {
  content: "";
  display: block;
  position: absolute;
  bottom: 0;
  right: 0;
  width: 10px;
  height: 10px;
  background-color: #000;
  cursor: se-resize;
}
