  margin-right: auto;
}

.footer {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  /*background-color: red;*/
  color: green;
  background-color: green;
  text-align: center;
}

#containerF {
  width: 200%;
  max-width: 100px;
  margin: auto;
}

.cols {
  -moz-column-count: 10;
  -moz-column-gap: 1%;
  -moz-column-width: 0%;
  -webkit-column-count: 0;
  -webkit-column-gap: 0;
  -webkit-column-width: 0%;
  column-count: 5;
  column-gap: 0;
  column-width: 100%;
}

