.grid-item-Entries {
  padding: 1%;
  background-color: rgba(195, 251, 255, 0.6);
  /* background: rgba(195, 251, 255, 0.6); */
  border-radius: 50%;
  width: 100px;
  height: 100px;
  text-align: center;
  /* Ellipse 41 */
}

.bubble_reflection {
  shape-outside: ellipse(20% 30%);
  clip-path: ellipse(30% 40%);
  width: 41.71px;
  height: 80.16px;
  background: rgba(217, 217, 217, 0.5);
  transform: rotate(30.96deg);
}

.grid-item-Entries:hover {
  border-radius: 50%;
  padding: 10px;
  background-color: rgba(195, 251, 255, 0.7);
  box-shadow: 0 2px 12px #f1dfc4;
}

